.Header {
    display: flex;
}

.home-logo {
    width: 14rem;
    height: 4rem;
    text-align: center;
    font: 1rem serif;
    font-weight: bold;
    color: white;
    background-color: rgb(196, 75, 45);
}

.search {
    text-align: center;
    height: 4rem;
    flex-grow: 2;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: rgba(249, 255, 245, 0.76);
}

.search_textbox {
    padding: 0;
    margin: 0;
    width: 80%;
    height: 50px;
}

.auth {
    display: flex;
    flex-direction: column;
    align-self: center;
    height: 4rem;
    flex-grow: 1;
    background-color: rgba(162, 106, 252, 0.76);
}
