.CreateModal {
    position: fixed;
    top: 30%;
    left: 30%;
    transform: (-50%, -50%);
    width: 320px;
    border-radius: 10px;
    background-color: gainsboro;
    box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
    display: flex;
    flex-direction: column;
}

.CreateModal-title {
    background-color: gray;
    text-align: center;
    color : white;
    font : 1.2rem solid bold;
}

.CreateModal-content {
    display: flex;
    flex-direction: column;
}

.createModal-content input {
    flex-grow:1;
}

.CreateModal-button-wrap {
    display:flex;
}

.CreateModal-button {
    flex-grow: 1;
}